import React from 'react';
import propTypes from 'prop-types';
import './App.css';
function Movie({year, title, summary, poster, gern}) {
    return ( <div className="movie"> <img className="photo"src={poster}></img>
    <div className="movie__data"><h3 className="title">{title}({year})</h3>
                <ul className="movie__genres">{gern.map(gerns => (<li>{gerns}</li>))}</ul>
               
                <p className="movie__summary">{summary.slice(0,240)}...</p></div></div>);
}

Movie.propTypes = {
    id : propTypes.number.isRequired,
    year : propTypes.number.isRequired,
    title : propTypes.string.isRequired,
    summary : propTypes.string.isRequired,
    poster : propTypes.string.isRequired,
    gern : propTypes.arrayOf(propTypes.string).isRequired
  };

  export default Movie;
